import React, {useState,useEffect} from 'react';
import { db } from '../firebase';

const Tbody = () => { 
    // le estoy panado prop data input

    const [usuarios,setUsuarios]= useState([]);
    
    useEffect(()=>{
        return db.collection('usuarios')
          .onSnapshot((snapshot)=>{
            const data = [];
               snapshot.forEach((doc)=>{
                    data.push(doc.data()); 
               })
               setUsuarios([...data]);
          })
       
        },[])

        const listaDatos = usuarios.length ? usuarios.map((usuario,index)=>{
            return (<tr key={index}>
              <td>{usuario.nombre}</td>
              <td>{usuario.oficio}</td>
              <td><button className="botonDelete"  >Delete</button></td>
              </tr> )
          })  :  <li>Que no hay usuarios</li>;

          

    return (
        <div className="tbody">
            <tbody>
               {listaDatos}
            </tbody>
            
            
        </div>
        
    );
};

export default Tbody;