import React from 'react';
import Tbody from './Tbody';
import Thead from './Thead';

const Table = ({db}) => {

    //valor inicial, funcionUpdate
// const [count, setCount] = useState()

    return (
        <div className="contenedorTabla">
            <table className="tabla">
                <Thead/>
                <Tbody db={db} />

            </table>
        </div>
    );


    

};




export default Table;