import React from 'react'; //Importando libreria
import NombreUsuario from './NombreUsuario';
import Avatar from './Avatar';

const Header = () => {

    const user = {
        nombre: "matias",
        apellido: "palacio"
    }


    return (

        

        <div>
            

            <header className="encabezado">
            <h1>REACT TRAINING</h1>
            <NombreUsuario user={user}/>
            {/* <div> <img className="imagen" src="https://www.flaticon.es/svg/static/icons/svg/2159/2159675.svg" alt="fuji"></img></div> */}
            <Avatar />
            </header>

        </div>
    );
    
};

export default Header;